@mixin font($fs, $fw, $lh, $ls){
    font-size: $fs;
    font-weight: $fw;
    line-height: $lh;
    letter-spacing: $ls;
}

@mixin mobile {
  @media screen and (min-width: 320px) {
    @content;
  }  
}

  @mixin tablet {
    @media screen and (min-width: 768px) {
      @content;
    }
  }

  @mixin desktop {
    @media screen and (min-width: 1200px) {
      @content;
    }
  }
  
  @mixin retina {
    @media (min-device-pixel-ratio: 2),
      (min-resolution: 192dpi),
      (min-resolution: 2dppx) {
      @content;
    }
  }